  includeEditors: ['editor-id-1'], // æŒ‡å®šè¯¥æ¨¡å—å¯¹å“ªäº›BraftEditorç”Ÿæ•ˆï¼Œä¸ä¼ æ­¤å±žæ€§åˆ™å¯¹æ‰€æœ‰BraftEditoræœ‰æ•ˆ
